.header{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}
.jumbo{
    margin-top:5%;
    text-align: center;
}
.jumbo > h1{
    font-size: xx-large;
}
.btn{
    text-decoration: none;
    color : white;
    background-color:  #3f51b5;
    border-radius: 99px;
    width : auto;
    font-size: large;
    padding : 10px;
    text-transform: uppercase;
}

.hero{
    display: flex;
    justify-content: center;
    justify-content: space-around;
}
.options{
    cursor: pointer;

}
.active {
    transition: 200ms all ease;
    color: black;
    border-bottom: 3px ridge  #3f51b5;
}
.lg{
    width: 100vw;
    background-color: #3f51b5;
}
.mediaup{
    display: flex;
    justify-content: space-between;
}
.up-area {
    width: 100%;
    height: 80vh;
    display: grid;
    place-items: center;
}
.cross{
    text-decoration: none;
}
.cross:hover{
    transition: 300ms all ease;
    background-color: rgb(182, 178, 178);
    cursor: pointer;
}