@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Allura&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Allura&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Allura&display=swap);
body {
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin : 0;
}
::-webkit-scrollbar{
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
    font-size: 10px;
}
body {
    background-color: whitesmoke;
}

.padd-u{
    padding-top: 2%;
}
.footer {
    background-color: rgb(95, 95, 95);
    color: white;
    overflow: hidden;
}

.left { 
    font-size: 40px;
    font-weight: 500;
}
h3{
    color : grey;
}
h4{
    color : rgb(255, 255, 255);
    cursor: pointer;
}
h4:hover {
    transition: 200ms all ease;
    color : #3f51b5;
}
.options {
    text-align: right;
}
.footer-row{
    border-bottom: 1px ridge grey;
}
.social-footer:hover{
    color : #3f51b5;
    transition: 200ms all ease;
    cursor: pointer;
}
.copyright {
    text-align: right;
}
@media screen and (max-width : 600px){
    .footer{
        text-align: center;
    }
    .options {
        text-align: center;
    }
    .copyright{
        text-align: center;
        padding-top: 5px;
    }
}
.end{
    padding-bottom: 3%;
}
.pictures{
    flex : 0.5 1;
}
.des {
    margin-top : 6%;
    text-align: right;
}
.desc{
    width: auto;
    align-items: centergb(0, 0, 0);
    overflow: hidden;
}
.padd-u{
    padding-top: 2%;
}
.padd-b{
    padding-bottom : 2%;
}
.container-c{
    margin-top : 2%;
    width: 100%;
    height: 100%;
    border-radius: 25px;
    margin-bottom : 2%;
}


.logo{
    width: 100%;
    text-align: left;
    color: rgb(32, 31, 31);
    font-family: 'Allura', cursive;
    font-size: 5vw;
}

.info{
    margin-top : 5%;
    color : black;
    font-weight: 900;
    font-size: 5vw;
}
.explaination {
    color : rgb(34, 31, 31);
    letter-spacing: 1px;
    font-size: large;
}
.steps{
    margin-top:2%;
    margin-bottom: 5%;
}

.step-desc{
    padding-top:5%;
    padding-bottom: 5%;
}
.step-1{
    background-color: whitesmoke;
}
.step-1:hover{
    box-shadow: 2px 2px 10px 1px rgba(0, 0, 0, 0.2);
    transition: 500ms all ease;
    
}
.step-2{
    background-color: whitesmoke;
    height: auto;
}
.step-2:hover{
    transition: 500ms all ease;
    box-shadow: 2px 2px 10px 1px rgba(0, 0, 0, 0.2);
    
}
.step-3{
    background-color: whitesmoke;
    height: auto;

}
.step-3:hover{
    transition: 500ms all ease;
    box-shadow: 2px 2px 10px 1px rgba(0, 0, 0, 0.2);
    
}
.goal{
    margin-top : 5%;
    margin-bottom: 5%;
    font-size: 3.5vw;
    text-align: center;
    padding-bottom : 5%;
    border-bottom: 1px ridge rgb(0, 0, 0);
}
.scroll-button-icon {
    background-color :#3f51b5;
    border-radius: 25px;
    padding : 7px;
    text-decoration: none;
}
.scroll-button{
    color : #3f51b5;
    letter-spacing: 1px;
    font-size: x-large;
    cursor: pointer;
}

.segments {
    margin-top : 51%;
}
.headings{
    text-transform: uppercase;
    color: #3f51b5;
    letter-spacing: 1px;
    font-size: large;
}
.overview {
    margin-top : 10%;
}

.socials {
    width: 100%;
    text-align: left;
}
.handles {
    color: rgb(122, 118, 118);
    align-self: center;
}
.handles:hover {
    transition : 400ms all ease;
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
    color : #3f51b5;
}

.overlay-img{
    position:"absolute";
    width:100%;
    left:0;
    top:0;
    bottom:0;
    height:100%;
    object-fit:cover;
    transform: translate(0%,0%);
    z-index:-2;
}
.centered{
    position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color :black;
  text-align: center;
  font-size: 5vw;
}


.header{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}
.jumbo{
    margin-top:5%;
    text-align: center;
}
.jumbo > h1{
    font-size: xx-large;
}
.btn{
    text-decoration: none;
    color : white;
    background-color:  #3f51b5;
    border-radius: 99px;
    width : auto;
    font-size: large;
    padding : 10px;
    text-transform: uppercase;
}

.hero{
    display: flex;
    justify-content: center;
    justify-content: space-around;
}
.options{
    cursor: pointer;

}
.active {
    transition: 200ms all ease;
    color: black;
    border-bottom: 3px ridge  #3f51b5;
}
.lg{
    width: 100vw;
    background-color: #3f51b5;
}
.mediaup{
    display: flex;
    justify-content: space-between;
}
.up-area {
    width: 100%;
    height: 80vh;
    display: grid;
    place-items: center;
}
.cross{
    text-decoration: none;
}
.cross:hover{
    transition: 300ms all ease;
    background-color: rgb(182, 178, 178);
    cursor: pointer;
}
.logo2{
    width: 100%;
    text-align: center;
    color: rgb(32, 31, 31);
    font-family: 'Allura', cursive;
    font-size: 80px;
}

